.formula-container {
  display: flex;
  position: absolute;
  bottom: 10%;
  right: 0;
  border: 1px solid #ccc;
  flex-direction: column;
  background-color: white;
}
.formula-class-container {
  display: flex;
  flex-direction: row;
  width: 300px;
  overflow: hidden;
  overflow-x: scroll;
  white-space: nowrap;
  background-color: #e7e7e7;
}
.formula-item-container {
  display: flex;
  flex-wrap: wrap;
  width: 300px;
  margin-bottom: 4px;
}
.formula-item {
  width: 100px;
  height: 100px;
  overflow: auto;
  margin: 0;
  background-color: white;
  box-sizing: border-box; /* �߿�������ڲ� */
  border: 1px solid #ccc;
}
.formula-item p {
  word-wrap: break-word; /* �������ʻ��� */
  word-break: break-all; /* ��ϳ������� */
  margin: 4px;
}
